import React , {useState} from 'react';
import styled from '@emotion/styled'
import { obtenerDiferenciaYear,calcularMarca,calcularPlan} from '../helper'


const Campo = styled.div`
        display:flex;
        align-items:center;
        margin-bottom:1rem;
`

const Label = styled.label`
        flex:0 0 100px;
`

const Select = styled.select`
        display:block;
        border: 1px solid #e1e1e1;
        width:100%;
        padding: 1rem;
        -webkit-appearance:none;

`

const InputRadio = styled.input`
        margin: 0 1rem;
`
const Boton = styled.button`
        background-color: #00838F;
        font-size:16px;
        width:100%;
        padding:1rem;
        text-transform:uppercase;
        font-weight:bold;
        border:none;
        transition:background-color 0.3s ease;
        margin-top:1rem;
        &:hover{
            background-color:#26C6DA;
            cursor: pointer;
        }
`
const Error = styled.div`
        background-color:red;
        color:white;
        padding:1rem;
        width:100%;
        text-align:center;
        margin-bottom:2rem;    
`

const Formulario = ({guardarResumen,guardarCargando}) => {

    const [datos,guardarDatos]=useState({
        marca:'',
        year:'',
        plan:''
    })

    const [error,guardarError]=useState(false)
    
    const obtnerInformacio = e =>{
        
        guardarDatos({
            ...datos,
            [e.target.name] : e.target.value
        })}

    const {marca,year,plan}=datos

    const cotizarSeguro = e=>{
        e.preventDefault();

        if(marca.trim()===''||year.trim()===''||plan.trim()===''){
            guardarError(true)
            return;
        }
        
        guardarError(false);

        //una base de 2.000

        let resultado=2000

        //obtener la diferencia en año

        const diferenciaYear = obtenerDiferenciaYear(year)
        
        //por cada año hay que restarle el 3%

        resultado -= ((diferenciaYear * 3) * resultado) / 100 ;

        
        //Americano 15%
        //Asiatico 5%
        //Europeo 30%
        resultado = calcularMarca(marca)*resultado

        

        //Basico aumenta 20%
        //Completo 50%
        const incrementoPlan = calcularPlan(plan);

        resultado = parseFloat(incrementoPlan*resultado).toFixed(2)

        guardarCargando(true)

        //total

        setTimeout(()=>{
            guardarCargando(false)
            guardarResumen({ cotizacion:resultado,
                datos })
        },3000)
       


    }

    return ( 

        
        <form
            onSubmit={cotizarSeguro}
        >
            {error? <Error>Todos los campos son obligatorios</Error>:null}
            <Campo>
                <Label>Marca</Label>
                <Select
                    name='marca'
                    value={marca}
                    onChange={obtnerInformacio}
                >
                    
                    <option value="">-- Seleccione --</option> 
                    <option value='americano'>Americano</option>
                    <option value='europeo'>Europeo</option>
                    <option value='asiatico'>Asiatico</option>
                </Select> 
            </Campo>

            <Campo>
                <Label>Año</Label>
                <Select
                    name='year'
                    value={year}
                    onChange={obtnerInformacio}
                >
                   
                            <option value="">-- Seleccione --</option>                   
                            <option value="2021">2021</option>
                            <option value="2020">2020</option>
                            <option value="2019">2019</option>
                            <option value="2018">2018</option>
                            <option value="2017">2017</option>
                            <option value="2016">2016</option>
                            <option value="2015">2015</option>
                            <option value="2014">2014</option>
                            <option value="2013">2013</option>
                            <option value="2012">2012</option>
                </Select> 
            </Campo>

            <Campo>
                <Label>Plan</Label>
                <InputRadio
                    name='plan'
                    type='radio'
                    value='basico'
                    checked={plan==='basico'}
                    onChange={obtnerInformacio}
                    />Basico

                <InputRadio
                    name='plan'
                    type='radio'
                    value='completo'
                    checked={plan==='completo'}
                    onChange={obtnerInformacio}
                    />Completo
            </Campo>
            <Boton type='submit' >Cotizar</Boton>
        </form>

     );
}
 
export default Formulario;